body {
  min-height: 100vh;
  background: #994fad; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #994fad 0%,#3960d6 70%,#3960d6 70%,#3960d6 100%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(315deg, #994fad 0%,#3960d6 70%,#3960d6 70%,#3960d6 100%);
  background: -o-linear-gradient(315deg, #994fad 0%,#3960d6 70%,#3960d6 70%,#3960d6 100%);
  background: linear-gradient(135deg, #994fad 0%,#3960d6 70%,#3960d6 70%,#3960d6 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#994fad', endColorstr='#3960d6',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  padding: 20px;
}

.suggestion-opaque>div>div {
  background: rgba(95, 35, 35, 0.5)!important;
  -webkit-transition: background 300ms;
  -o-transition: background 300ms;
  transition: background 300ms;
  border: 2px solid white;
}
.suggestion-opaque>div>div:hover {
  background: rgba(255, 255, 255)!important;
}
.vertical-timeline-element-date {
  color: white;
}

.react-datepicker-wrapper input {
  padding: 8px 6px;
  cursor: pointer;
}

.title-pop-shadow {
  -webkit-animation:  glow 3s ease-in-out;
          animation:  glow 3s ease-in-out;
}

@-webkit-keyframes glow {
  0% {
    text-shadow:  0 0 10px white;
  }

  15% {
    text-shadow: 2px 2px 10px rgba(255, 255, 255, 1),
    -2px -2px 10px rgba(255, 255, 255, 1);
  }
  30% {
    text-shadow: 2px 2px 4px rgba(255, 255, 255, .7),
    -2px -2px 4px rgba(255, 255, 255, .7);
  }
  50% {
    text-shadow: 20px 20px 50px rgba(255, 255, 255, .5),
    -20px -20px 50px rgba(255, 255, 255, .5);
  }
}

@keyframes glow {
  0% {
    text-shadow:  0 0 10px white;
  }

  15% {
    text-shadow: 2px 2px 10px rgba(255, 255, 255, 1),
    -2px -2px 10px rgba(255, 255, 255, 1);
  }
  30% {
    text-shadow: 2px 2px 4px rgba(255, 255, 255, .7),
    -2px -2px 4px rgba(255, 255, 255, .7);
  }
  50% {
    text-shadow: 20px 20px 50px rgba(255, 255, 255, .5),
    -20px -20px 50px rgba(255, 255, 255, .5);
  }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




